* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: monospace;
}

.body {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(217deg, rgba(255, 0, 0, 0.8), rgba(255, 0, 0, 0) 70.71%), linear-gradient(127deg, rgba(0, 255, 0, 0.8), rgba(0, 255, 0, 0) 70.71%), linear-gradient(336deg, rgba(0, 0, 255, 0.8), rgba(0, 0, 255, 0) 70.71%);
}

.stats {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 30vh;
  width: 30%;
  position: absolute;
  top: 10px;
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.2);
  gap: 15px;
}
.stats .correct-amount {
  color: rgb(0, 145, 12);
}
.stats .incorrect-amount {
  color: rgb(255, 0, 0);
}
.stats .btn {
  font-size: 20px;
  padding: 5px;
  background: linear-gradient(217deg, rgba(255, 0, 0, 0.8), rgba(255, 0, 0, 0) 70.71%), linear-gradient(127deg, rgba(0, 255, 0, 0.8), rgba(0, 255, 0, 0) 70.71%), linear-gradient(336deg, rgba(0, 0, 255, 0.8), rgba(0, 0, 255, 0) 70.71%);
  color: white;
  border: 1px solid black;
  cursor: pointer;
}

.stats > div {
  font-size: 20px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 35vh;
  height: 60vh;
  width: 70%;
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.3);
}
.container .quote-text {
  position: absolute;
  top: 10px;
  font-size: 17px;
  width: 90%;
  height: min-content;
}
.container .quote-area {
  position: absolute;
  bottom: 10px;
  height: 300px;
  width: 90%;
  resize: none;
  padding: 5px;
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0.3);
}

.correct {
  color: rgb(0, 145, 12);
}

.incorrect {
  color: rgb(255, 0, 0);
}